@mixin inEachBox {
  min-height: 25rem;
  background: $background;
  border-radius: 30px;
  position: relative;
  box-shadow: inset 2px 2px 6px rgba(255, 255, 255, 0.15);
  padding: 24px;
}

@mixin icon {
  .icon {
    display: inline-block;
    position: absolute;
    bottom: 2.4rem;
    right: 2.4rem;
    opacity: 0.3;
    z-index: 5;
    transition: 0.4s;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }
}
